import { RouterModule, Routes } from '@angular/router';
import { PublicInfoComponent } from './public-info.component';
import { ModuleWithProviders } from '@angular/core';


const publicInfoRoutes: Routes = [
    {
        path: '', component: PublicInfoComponent
    }
];
export const publicInfoRouting: ModuleWithProviders = RouterModule.forChild(publicInfoRoutes);



