import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PublicInfoComponent } from './public-info.component';
import { publicInfoRouting } from './public-info.routing';
import {SharedModule} from '../shared/shared.module';
import {FormsModule} from '@angular/forms';
import { VehicleService } from '../services/vehicle.service';
import {MatInputModule, MatSelectModule, MatToolbarModule} from '@angular/material';
import { TravelService } from '../services/travel.service';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    publicInfoRouting,
    MatInputModule,
    MatSelectModule,
    MatToolbarModule
  ],
  declarations: [PublicInfoComponent],
  providers: [
    VehicleService,
    TravelService
  ]
})
export class PublicInfoModule { }



