import { Component, OnInit } from '@angular/core';
import {VehicleService} from '../services/vehicle.service';
import {TravelService} from '../services/travel.service';

@Component({
  selector: 'app-public-info',
  templateUrl: './public-info.component.html',
  styleUrls: ['./public-info.component.scss']
})
export class PublicInfoComponent implements OnInit {

  protected header = ['Setor Requisitante', 'Destino', 'VeÃ­culo', 'Data Ida', 'Data Volta'];

  protected model = {car: '', dataIni: '', dataFim: ''};
  protected carList = [];
  protected onGoingTravels = [];
  protected plannedTravels = [];
  protected travels = [];

  constructor(private vehicleService: VehicleService,
              private travelService: TravelService) {

  }


  ngOnInit() {

    this.vehicleService.vehiclesObs.subscribe((data) => {
      this.carList = data;
    });

    this.showTravels();

  }

  showTravels() {
    this.travelService.getAllTravels().subscribe((data) => {
      this.travels = data;
    }, error => {
      console.error(error);
    });
  }

}



